.footer {
  position: relative;
  z-index: 2;
}

.footer-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: toRem(32);
  border-bottom: toRem(1) solid $grey-500;
}

.footer-logo {
  display: block;
  width: toRem(190);
  height: toRem(36);
  text-decoration: none;
  margin: 0;

  &__img {
    object-fit: contain;
  }
}

.media {
  display: flex;
  align-items: center;
  gap: toRem(12);

  &__link {
    display: block;
    width: toRem(40);
    height: toRem(40);
    border-radius: toRem(8);
    background-color: $primary-700;
    padding: toRem(12);
    transition: background-color 0.3s;

    &:hover {
      background-color: $primary-600;
    }
  }
}

.footer-nav {
  padding-bottom: toRem(32);
  border-bottom: toRem(1) solid $grey-500;
}

.footer-nav-list {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 0 toRem(16);
  margin-top: toRem(32);

  @media (max-width: $tablet) {
    flex-direction: column;
    gap: toRem(32) 0;

    li {
      &.active {
        & > a {
          &:after {
            transform: translateY(-50%) rotate(-180deg);
          }
        }

        .footer-sub-menu {
          display: block;
        }
      }
    }
  }

  & > li > a {
    display: block;
    font-size: toRem(20);
    font-weight: 600;
    line-height: toRem(26);
    color: $text-color;
    text-decoration: none;
    cursor: default;
    pointer-events: none;

    @media (max-width: $tablet) {
      position: relative;
      cursor: pointer;
      pointer-events: none;

      &:after {
        content: "";
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        width: toRem(16);
        height: toRem(16);
        background: url("../img/icons/arrow-down.svg") no-repeat center;
        transition: transform 0.3s;
      }
    }

    @media (max-width: $mobile) {
      font-size: toRem(18);
      line-height: toRem(28);
    }
  }

  & > li {
    @media (max-width: $tablet) {
      width: 100%;
    }
  }
}

.footer-sub-menu {
  margin-top: toRem(20);

  @media (max-width: $tablet) {
    display: none;
  }

  li {
    &:not(:last-child) {
      margin-bottom: toRem(16);
    }
  }

  a {
    font-size: toRem(16);
    line-height: toRem(26);
    color: $text-color;
    text-decoration: none;
    transition: color 0.3s;

    &:hover {
      color: $primary-500;
    }
  }

  &.active {
    position: relative;
    left: 0;
    visibility: visible;
    z-index: 1;
  }
}
