.benefits-cards {
  display: grid;
  grid-template-areas:
    "small-card-1 big-card card-small-2"
    "card-small-3 big-card card-small-4";
  grid-template-columns: repeat(3, 1fr);
  grid-gap: toRem(24);
  margin-top: toRem(24);

  @media (max-width: $tablet) {
    grid-template-areas:
    "small-card-1 big-card"
    "card-small-2 big-card"
    "card-small-3 card-small-4";
    grid-template-columns: repeat(2, 1fr);
    grid-gap: toRem(24) toRem(16);
    margin-top: toRem(18);
  }

  @media (max-width: $mobile) {
    grid-template-areas:
    "small-card-1"
    "big-card"
    "card-small-2"
    "card-small-3"
    "card-small-4";
    grid-template-columns: 1fr;
    grid-gap: toRem(12) toRem(0);
    margin-top: toRem(12);
  }
}

.benefits-card {
  position: relative;
  border-radius: toRem(12);
  border: toRem(1) solid $primary-700;
  background-color: $primary-900;
  box-shadow: $element-shadow;
  overflow: hidden;

  &:after {
    content: "";
    @include blur-box(30px);
    width: 100%;
    height: 100%;
    transform: translate(-100%, -100%) translate3d(0, 0, 0);
    opacity: 0.06;
    transition: all cubic-bezier(1, 0, 0, 1) 1500ms;
  }

  &:hover {
    &:after {
      transform: translate(-50%, -50%) translate3d(0, 0, 0);
    }
  }

  &__title {
    position: relative;
    font-size: toRem(18);
    font-weight: 500;
    line-height: toRem(28);
    color: $white-color;
    padding: toRem(24);
    z-index: 3;
  }

  &__animate {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;

    @include max-tablet {
      display: none;
    }
  }

  &__static-img {
    display: none;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 2;

    @include max-tablet {
      display: block;
    }
  }
}

.card-small-1 {
  grid-area: small-card-1;
}

.card-big {
  grid-area: big-card;
}

.card-small-2 {
  grid-area: card-small-2;
}

.card-small-3 {
  grid-area: card-small-3;
}

.card-small-4 {
  grid-area: card-small-4;
}

.card-small-1,
.card-small-2,
.card-small-3,
.card-small-4 {
  height: toRem(288);
}

.card-big {
  height: toRem(600);
}

// Animation
.benefits-card {
  &__animate-element {
    position: absolute;
    transition: all cubic-bezier(1, 0, 0, 1) 1500ms;
  }

  // The first animate block
  .img-cub-small,
  .img-cub-big {
    top: 45%;
    left: 40%;
    z-index: 1;
  }

  .img-cub-big {
    width: toRem(80);
    height: toRem(80);
  }

  .img-cub-small {
    width: toRem(90);
    height: toRem(90);
  }

  .text-animate {
    top: 45%;
    left: 0;
    width: 100%;
    font-size: toRem(80);
    font-weight: 700;
    line-height: normal;
    text-transform: uppercase;
    color: $white-color;
    transform: matrix(1, 0, 0, 1, 0, 0);
    opacity: 0;
    z-index: 2;
  }

  .img-cub-1 {
    transform: rotate(60deg) translateY(-80px) rotate(-50deg) matrix(1, 0, 0, 1, 0, 0);
  }

  .img-cub-2 {
    transform: rotate(120deg) translateY(-80px) rotate(-110deg) matrix(1, 0, 0, 1, 0, 0);
  }

  .img-cub-3 {
    transform: rotate(180deg) translateY(-80px) rotate(-170deg) matrix(1, 0, 0, 1, 0, 0);
  }

  .img-cub-4 {
    transform: rotate(240deg) translateY(-80px) rotate(-230deg) matrix(1, 0, 0, 1, 0, 0);
  }

  .img-cub-5 {
    transform: rotate(300deg) translateY(-80px) rotate(-290deg) matrix(1, 0, 0, 1, 0, 0);
  }

  .img-cub-6 {
    transform: rotate(360deg) translateY(-80px) rotate(-350deg) matrix(1, 0, 0, 1, 0, 0);
  }

  // The second animate block
  .img-gun-1 {
    width: toRem(248);
    height: toRem(202);
    top: 18%;
    left: 10%;
    transform: matrix(0.92976, -0.36817, 0.36817, 0.92976, -54.7773, -1.64631);
  }

  .img-gun-2 {
    width: toRem(165);
    height: toRem(165);
    top: 45%;
    left: 0;
    transform: matrix(1,0,0,1,17.80936, -22.91366);
  }

  .img-gun-3 {
    width: toRem(240);
    height: toRem(210);
    bottom: 0;
    left: 0;
    transform: matrix(0.97712, -.1, 0.1, 0.97712, 5, 13);
    opacity: 1;
  }

  .img-gun-4 {
    width: toRem(236);
    height: toRem(236);
    right: 0;
    bottom: 45%;
    transform: matrix(-0.89703, -0.44198, -0.44198, 0.89703, 17, 173);
    opacity: 1;
  }

  .img-knife {
    width: toRem(197);
    height: toRem(197);
    top: 18%;
    right: 0;
    transform: matrix(0.99089, 0.13468, -0.13468, 0.99089, 15.905, -31.0739);
    opacity: 1;
  }

  // The third animate block
  &__animate-box {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    height: 78%;
  }

  .img-j-left,
  .img-j-right {
    position: absolute;
    width: 170px;
    height: 114px;
    top: 50%;
    transform: translate(0, -50%);
  }

  .img-j-left {
    left: 0;
  }

  .img-j-right {
    right: 0;
  }

  .img-center {
    position: absolute;
    width: 219px;
    height: 134px;
    top: 50%;
    left: 53%;
    transform: translate(-50%, -50%) scale(0);
  }

  // The fourth animate block
  .img-casino-1,
  .img-casino-2,
  .img-casino-3 {
    bottom: 0;
  }

  .img-casino-1 {
    width: toRem(134);
    height: toRem(134);
    left: 0;
    transform: matrix(1, 0, 0, 1, 0, 0);
  }

  .img-casino-2 {
    width: toRem(300);
    height: toRem(170);
    left: 20%;
    transform: matrix(1, 0, 0, 1, 0, 0);
  }

  .img-casino-3 {
    width: toRem(188);
    height: toRem(188);
    right: 0;
    transform: matrix(1, 0, 0, 1, 0, 0);
  }

  // The fifth animate block
  .img-gift-1,
  .img-gift-3 {
    bottom: 0;
  }

  .img-gift-1 {
    width: toRem(201);
    height: toRem(201);
    left: 0;
    transform: matrix(1, 0, 0, 1, 0, 0);
  }

  .img-gift-2 {
    top: 25%;
    left: 40%;
    width: toRem(167);
    height: toRem(150);
    transform: matrix(1, 0, 0, 1, 0, 0);
  }

  .img-gift-3 {
    right: 0;
    width: toRem(144);
    height: toRem(144);
    transform: matrix(1, 0, 0, 1, 0, 0);
  }

  &:hover {
    // The first animate block
    .text-animate {
      transform: matrix(0.5, 0, 0, 0.5, 0, 0);
      opacity: 1;
    }

    .img-cub-1 {
      transform: rotate(60deg) translateY(-80px) rotate(-50deg) matrix(.90085,.43413,-.43413,.90085,59,35);
    }

    .img-cub-2 {
      transform: rotate(120deg) translateY(-80px) rotate(-110deg) matrix(.93624,-.35135,.35135,.93624,30,33);
    }

    .img-cub-3 {
      transform: rotate(180deg) translateY(-80px) rotate(-170deg) matrix(0.92705, 0.37493, -0.37493, 0.92705, -100,16);
    }

    .img-cub-4 {
      transform: rotate(240deg) translateY(-80px) rotate(-230deg) matrix(0.96118, -0.27346, 0.27346, 0.96118, -77, -17);
    }

    .img-cub-5 {
      transform: rotate(300deg) translateY(-80px) rotate(-290deg) matrix(.95691,-.29037,.29037,.95691,-51,-10);
    }

    .img-cub-6 {
      transform: rotate(360deg) translateY(-80px) rotate(-350deg) matrix(.91531,.40275,-.40275,.91531,100,-3);
    }

    // The second animate block
    .img-gun-1 {
      transform: matrix(0.75, -0.29454, 0.29454, 0.75, -74, -34);
      opacity: 0.4;
      filter: blur(2.5px);
    }

    .img-gun-2 {
      transform: matrix(-1.7,-0.01,.01,1.7,100,-5);
    }

    .img-gun-3 {
      transform: matrix(.7566,-.06667,.06667,.7566,-64,76);
      opacity: 0.4;
      filter: blur(2.5px);
    }

    .img-gun-4 {
      transform: matrix(-.89703,-.3315,-.3315,.89703,39,320);
      opacity: 0.4;
      filter: blur(2.5px);
    }

    .img-knife {
      transform: matrix(.85919, .26794, -.26794, .85919, 30,-70);
      filter: blur(2.5px);
    }

    // The third animate block
    .img-j-left {
      transform: translate(0, -75%);
    }

    .img-j-right {
      transform: translate(0, -25%);
    }

    .img-center {
      transform: translate(-50%, -50%) scale(1);
    }

    // The fourth animate block
    .img-casino-1 {
      transform: matrix(0.94693, 0.32114, -0.32114, 0.94693, 0, 0);
      filter: blur(2.5px);
    }

    .img-casino-2 {
      transform: matrix(1.4, 0, 0, 1.4, -15, -11);
    }

    .img-casino-3 {
      transform: matrix(.41,.5625,-.5625,.41,-2,-50);
      filter: blur(2.5px);
    }

    // The fifth animate block
    .img-gift-1 {
      transform: matrix(.6, 0, 0, .6, -29, 33);
      filter: blur(2.5px);
    }

    .img-gift-2 {
      transform: matrix(1.6, 0, 0, 1.6, -20, 30);
    }

    .img-gift-3 {
      transform: matrix(.7,0,0,.7,15,22);
      filter: blur(2.5px);
    }
  }
}
