.header {
  background-color: $primary-900;
  border-bottom: .1rem solid $primary-700;
  padding: toRem(15) 0;
  position: relative;
  z-index: 5;

  @media (max-width: $tablet) {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: toRem(77);
    padding: toRem(18) 0;
    z-index: 10;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__wrap {
    display: flex;
    align-items: center;
    gap: toRem(40);

    @media (max-width: 1200px) {
      gap: toRem(16);
    }

    @media (max-width: $tablet) {
      gap: toRem(24);
    }
  }

  &__nav {
    ul {
      list-style: none;
    }

    a {
      color: $white-color;
    }

    @media (max-width: $tablet) {
      position: fixed;
      top: toRem(-77);
      left: 0;
      width: 100%;
      height: 0;
      opacity: 0;
      padding: toRem(40) toRem(32) toRem(105) toRem(32);
      background-color: $primary-900;
      z-index: -1;
      overflow-x: auto;
      pointer-events: none;
      @include custom-scroll-bar;
      transition: height 0.3s;

      &.active {
        pointer-events: auto;
        height: 100%;
        top: toRem(77);
        opacity: 1;
      }
    }

    @media (max-width: $mobile) {
      padding: toRem(40) toRem(16) toRem(105) toRem(16);
    }
  }

  &__lang {
    color: $text-color;
  }

  &__menu-btn {
    display: none;
    position: relative;
    width: toRem(40);
    height: toRem(40);
    border-radius: toRem(8);
    border: toRem(1) solid $primary-600;
    background-color: initial;
    padding: toRem(8);
    cursor: pointer;

    &:after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      width: toRem(24);
      height: toRem(24);
      transform: translate(-50%, -50%);
      background-image: url("../img/icons/menu.svg");
      background-repeat: no-repeat;
      background-size: contain;
    }

    &.active {
      &:after {
        background-image: url("../img/icons/close.svg");
      }
    }

    @media (max-width: $tablet) {
      display: block;
    }
  }

  &__desktop-nav {
    display: flex;
    align-items: center;
    position: relative;
    gap: toRem(40);
    font-weight: 500;
    z-index: 10;

    @media (max-width: 1200px) {
      gap: toRem(18);
    }

    @media (max-width: $tablet) {
      display: none;
    }
  }

  &__desktop-nav-item {
    &.has-drop {
      & > a {
        &:after {
          content: "";
          position: absolute;
          top: 50%;
          right: toRem(-8);
          width: toRem(16);
          height: toRem(16);
          transform: translateY(-50%);
          background: url("../img/icons/arrow-down.svg") no-repeat center;
          transition: transform 0.3s;
        }
      }

      &:hover {
        .header__desktop-nav-drop {
          display: flex;
        }

        .header__desktop-nav-link {
          &:after {
            transform: translateY(-50%) rotate(-180deg);
          }
        }
      }
    }
  }

  &__desktop-nav-drop {
    display: none;
    align-items: flex-start;
    gap: toRem(28);
    position: absolute;
    top: 206%;
    left: 0;
    background-color: $primary-900;
    border-radius: toRem(12);
    border: toRem(1) solid $primary-700;
    padding: toRem(16);

    &:after {
      content: "";
      position: absolute;
      top: toRem(-30);
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
    }
  }

  &__desktop-nav-link {
    display: block;
    position: relative;
    padding: 0 toRem(27) 0 0;
    text-decoration: none;

    @media (max-width: 1200px) {
      padding: 0 toRem(22) 0 0;
    }
  }

  &__desktop-nav-tabs-controls {
    min-width: toRem(175);
  }

  &__desktop-nav-tabs-btn {
    display: block;
    position: relative;
    border-radius: .8rem;
    text-decoration: none;
    padding: toRem(12) toRem(15);
    transition: box-shadow .3s, background-color .3s;

    &:not(:last-child) {
      margin-bottom: toRem(12);
    }

    &::after {
      content: '';
      position: absolute;
      left: 100%;
      top: 50%;
      transform: translateY(-50%);
      width: 0;
      height: 0;
      border: 1rem solid transparent;
      border-left-color: $primary-700;
      opacity: 0;
      border-right: 0;
      transition: opacity .3s;
    }

    &.active {
      box-shadow: $element-shadow;
      background-color: $primary-700;

      &::after {
        opacity: 1;
      }

    }

    &:hover {
      box-shadow: $element-shadow;
      background-color: $primary-700;

      &::after {
        opacity: 1;
      }
    }
  }

  &__desktop-nav-tabs-panel {
    display: none;
    grid-template-columns: repeat(3, 1fr);
    gap: toRem(8) toRem(24);

    &.active {
      display: grid;
    }
  }

  &__desktop-nav-tab-btn {
    display: flex;
    align-items: center;
    gap: toRem(8);
    padding: toRem(12) toRem(16);
    border-radius: toRem(8);
    text-decoration: none;
    transition: .3s;

    &:hover {
      box-shadow: $element-shadow;
      background-color: $primary-700;
    }

    img {
      display: block;
      width: toRem(32);
      height: toRem(32);
      object-fit: contain;
    }
  }

  &__mobile-nav {
    display: none;

    ul a {
      text-decoration: none;
    }


    @media (max-width: $tablet) {
      display: block;
    }
  }

  &__nav-item {
    &:not(:last-of-type) {
      margin-bottom: toRem(32);
    }
  }

  &__nav-drop-item {
    &:not(:last-of-type) {
      margin-bottom: toRem(20);
    }

    &.active {
      .header__nav-end-item {
        display: block;
      }

      .header__nav-drop-link {
        &:after {
          transform: rotate(-180deg);
        }
      }
    }
  }

  ul &__nav-item-link {
    font-size: toRem(20);
    font-weight: 600;
    color: $primary-500;
  }

  &__nav-drop,
  &__nav-end {
    margin-top: toRem(20);
  }

  &__nav-end-item {
    display: none;

    &:not(:last-child) {
      margin-bottom: toRem(16);
    }
  }

  &__nav-drop-link {
    display: block;
    position: relative;
    font-weight: 600;

    &:after {
      content: "";
      position: absolute;
      top: 50%;
      right: 0;
      width: toRem(16);
      height: toRem(16);
      transform: translateY(-50%);
      background: url("../img/icons/arrow-down.svg") no-repeat center;
      transition: transform 0.3s;
    }
  }

  &__nav-end-link {
    display: block;
    font-weight: 400;

    img {
      display: none;
    }
  }
}

.logo {
  display: block;
  width: 16.8rem;
  height: 3.2rem;
  text-decoration: none;

  &__img {
    object-fit: contain;
  }
}
