.accordion {
  &__item {
    &.active {
      .accordion__header {
        &:after {
          background: url("../img/icons/minus.svg") no-repeat center;
        }
      }

      .accordion__content {
        grid-template-rows: 1fr;
        opacity: 1;
      }

      .content-top-indent {
        margin-top: toRem(16);
      }
    }
  }

  &__header {
    display: block;
    position: relative;
    cursor: pointer;

    &:after {
      content: "";
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0 -50%);
      width: toRem(24);
      height: toRem(24);
      background: url("../img/icons/plus.svg") no-repeat center;
    }
  }

  &__content {
    display: grid;
    grid-template-rows: 0fr;
    overflow: hidden;
    opacity: 0;
    transition: all .3s
  }

  &__wrapper {
    min-height: 0;
  }

  .content-top-indent {
    margin-top: 0;
    transition: margin-top .3s
  }
}
