.gambling-sites-header {
  text-align: center;
  margin-top: toRem(40);

  @media (max-width: $tablet) {
    margin-top: toRem(120);
  }

  &__title {
    h1 {
      font-size: toRem(52);
      font-weight: 700;
      line-height: toRem(64);
      color: $white-color;

      @media (max-width: $mobile) {
        font-size: toRem(36);
        line-height: toRem(48);
      }
    }
  }

  &__description {
    width: 100%;
    max-width: toRem(900);
    margin: toRem(24) auto 0;

    @media (max-width: $mobile) {
      margin: toRem(12) auto 0;
    }

    p {
      font-weight: 400;
      color: $text-color;

      &:not(:last-child) {
        margin-bottom: toRem(12);
      }
    }
  }
}

.gambling-sites-links-list {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: toRem(40);
  gap: toRem(16);

  &__item {
    display: block;
    padding: toRem(8) toRem(24);
    text-decoration: none;
  }
}
