.why-use {
  margin-top: toRem(12);

  &__item {
    border-radius: toRem(12);
    border: toRem(1) solid $primary-700;
    background-color: $primary-900;
    padding: toRem(24);
    font-weight: 400;
    color: $text-color;

    &:not(:last-child) {
      margin-bottom: toRem(12);
    }

    h4 {
      font-size: toRem(22);
      font-weight: 600;
      line-height: toRem(30);
    }
  }

  ul {
    list-style-type: disc;
    margin-top: toRem(12);
    padding-left: toRem(16);

    li {
      color: $text-color;
      padding-left: 0;

      &:before {
        display: none;
      }
    }
  }
}
