.top-rated {
  display: flex;
  align-items: flex-start;
  gap: toRem(16);

  @media (max-width: $mobile) {
    flex-direction: column;
  }

  &__media-box {
    display: flex;
    flex-shrink: 0;
    width: toRem(185);
    height: toRem(124);
    border-radius: toRem(8);
    background-color: $primary-900;
  }

  &__media {
    max-width: toRem(166);
    max-height: toRem(58);
    margin: auto;
  }

  &__media-img {
    object-fit: contain;
  }

  &__content {
    h5 {
      font-size: toRem(18);
      font-weight: 600;
      color: $white-color;
      margin-bottom: toRem(8);
    }

    p {
      font-weight: 400;
      color: $text-color;
    }
  }
}
