.info-section {
  position: relative;
  margin-top: toRem(48);
  padding-bottom: toRem(24);
  border-bottom: toRem(1) solid $grey-500;
  z-index: 4;

  &__action {
    position: relative;
    z-index: 1;
  }

  &__button {
    display: flex;
    align-items: center;
    gap: toRem(12);
    padding: toRem(12) toRem(24);
    text-decoration: none;
  }

  &__button-icon {
    width: toRem(24);
    height: toRem(24);
  }

  &__action-tooltip {
    position: absolute;
    top: 140%;
    left: 50%;
    transform: translateX(-50%);
    width: toRem(303);
    opacity: 0;
    z-index: -1;
    transition: opacity 0.3s;
    pointer-events: none;

    &::after {
      @media (max-width: 1330px) {
        left: 98%;
        top: 50%;
        transform: translateY(-50%) rotate(90deg);
        margin: 0;
      }

      @media (max-width: $mobile) {
        top: 1.5%;
        left: 50%;
        margin-left: toRem(-13);
        margin-top: toRem(-13);
        transform: rotate(0);
      }
    }

    @media (max-width: 1330px) {
      top: 50%;
      right: 110%;
      left: auto;
      transform: translate(0, -50%);
    }

    @media (max-width: $mobile) {
      top: 140%;
      left: 50%;
      transform: translateX(-32%);
    }
  }
}

.info-section__action {
  &:hover {
    .info-section__action-tooltip {
      opacity: 1;
      z-index: 3;
    }
  }
}
