@import "vars";
@import "media";
@import "utiles";
@import "mixins";

*,
*:before,
*:after {
  box-sizing: border-box;
}

html,
body {
  scroll-behavior: smooth;
}

html {
  font-size: 62.5%;
}

body {
  min-width: toRem(375);
  font: toRem(16)/toRem(26) $main-font;
  font-style: normal;
  color: $text-color;
  background-color: $main-bg;
}

video {
  border: 0;
  outline: none;
}

img {
  outline: none;
  max-width: 100%;
}

.img {
  display: flex;
  width: 100%;
  height: 100%;
}

.container {
  width: 100%;
  max-width: toRem(1200);
  margin: 0 auto;

  @media (max-width: $medium) {
    padding: 0 toRem(20);
  }

  @media (max-width: $tablet) {
    padding: 0 toRem(32);
  }

  @media (max-width: $mobile) {
    padding: 0 toRem(16);
  }
}

._bold {
  font-weight: 700;
}

.header-section {
  margin-top: toRem(40);

  @media (max-width: $tablet) {
    margin-top: toRem(120);
  }
}

.section-content {
  h3 {
    font-size: toRem(28);
    font-weight: 700;
    line-height: toRem(36);
    color: $white-color;

    @media (max-width: $mobile) {
      font-size: toRem(26);
    }
  }

  p {
    font-weight: 400;
    margin-top: toRem(12);
    color: $text-color;
  }

  &__link {
    color: $primary-500;

    &:hover {
      text-decoration: none;
    }
  }
}

.mt-section {
  margin-top: toRem(96);

  @media (max-width: $tablet) {
    margin-top: toRem(80);
  }

  @media (max-width: $mobile) {
    margin-top: toRem(48);
  }
}

.mt-48-section {
  margin-top: toRem(48);
}

.title {
  font-size: toRem(52);
  line-height: toRem(64);
  color: $white-color;
  font-weight: 700;

  @include max-mobile {
    font-size: toRem(36);
    line-height: toRem(48);
  }

  &--02 {
    font-size: toRem(28);
    line-height: toRem(36);

    @include max-mobile {
      font-size: toRem(26);
    }
  }
}
