.breadcrumbs {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: toRem(24);
  color: $white-color;
  gap: toRem(4) toRem(15);

  &__link {
    color: $primary-100;
    display: block;
    text-decoration: none;
    word-break: break-word;
    transition: color 0.15s linear;

    &:hover {
      color: $white-color;
    }
  }

  &__item {
    position: relative;
    word-break: break-word;

    & + & {
      &:before {
        content: "/";
        position: absolute;
        left: toRem(-11);
        top: 0;
        color: $primary-100;
      }
    }
  }
}
