.combo-box {
  position: relative;
  border-radius: toRem(12);
  border: toRem(1) solid $primary-700;
  background-color: $primary-900;
  padding: toRem(24);

  &:before {
    content: "";
    @include blur-box(200px);
    width: 40%;
    height: 100%;
    left: 0;
    transform: translate(-5%, -50%) translate3d(0, 0, 0);
  }

  &:hover {
    &:before {
      opacity: 0.3;
    }
  }

  &__header,
  &__content {
    position: relative;
    z-index: 2;
  }

  &__header-wrap {
    flex-grow: 1;
  }

  &__header-btn {
    position: relative;
    display: block;
    min-width: toRem(148);
    font-size: toRem(20);
    font-weight: 600;
    line-height: toRem(26);
    color: $white-color;
    background-color: transparent;
    border-radius: toRem(8);
    border: toRem(2) dashed $secondary-500;
    padding: toRem(12) toRem(24);
    margin-left: auto;
    transition: background .25s, border-color .25s;
    cursor: pointer;
    z-index: 2;

    &:hover {
      background: $primary-500;
      border-color: $primary-500;

      .combo-box__tooltip {
        opacity: 1;
        z-index: 2;
      }
    }
  }

  &__tooltip {
    display: block;
    position: absolute;
    top: 147%;
    left: 50%;
    width: 100%;
    transform: translateX(-50%);
    background-color: $primary-700;
    opacity: 0;
    z-index: -1;
    transition: opacity 0.3s;

    &::after {
      top: 11%;
      border-bottom-color: $primary-700;
    }
  }

  &__header {
    display: flex;
    align-items: center;
    gap: toRem(8);

    h4 {
      font-size: toRem(18);
      font-weight: 600;
    }
  }

  &__icon {
    flex-shrink: 0;
    width: toRem(24);
    height: toRem(24);

    .img {
      object-fit: cover;
      margin-top: 0;
    }
  }

  &__content {
    position: relative;
    font-weight: 400;
    color: $text-color;
    margin-top: toRem(8);
    z-index: 1;
  }

  &__link {
    color: $text-color;
    transition: color 0.3s;

    &:hover {
      color: $primary-500;
    }
  }
}
