.user-info {
  display: flex;
  align-items: flex-start;
  gap: toRem(16);
  border-radius: toRem(10);
  background-color: $primary-900;
  padding: toRem(24);

  @media (max-width: $mobile) {
    flex-direction: column;
  }

  &__photo {
    flex-shrink: 0;
    width: toRem(120);
    height: toRem(120);

    img {
      border-radius: toRem(10);
      object-fit: cover;
    }
  }

  &__content {
    h4 {
      font-size: toRem(28);
      font-weight: 700;
      line-height: toRem(36);
      color: $white-color;
    }
  }

  &__link {
    a {
      text-decoration: none;
      color: $text-color;
      margin-top: toRem(12);

      &:hover {
        text-decoration: underline;
      }
    }
  }

  &__text {
    p {
      color: $text-color;
      margin-top: toRem(12);
    }
  }

  &__actions {
    display: flex;
    align-items: center;
    margin-top: toRem(12);
    gap: toRem(12);
  }
}
