.button {
  display: block;
  position: relative;
  font: 600 toRem(14)/toRem(24) $main-font;
  text-transform: uppercase;
  color: $white-color;
  border-radius: toRem(8);
  border: none;
  cursor: pointer;
  text-decoration: none;
  background: transparent;
  transition: background 0.25s;
  z-index: 1;
  text-align: center;

  &:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: $button-bg;
    border-radius: toRem(8);
    z-index: -1;
    transition: opacity 0.25s linear;
  }

  &:hover {
    background: $primary-500;

    &:before {
      opacity: 0;
    }
  }

  &__small {
    padding: toRem(8) toRem(24);
  }

  &--normal,
  &__big {
    padding: toRem(12) toRem(24);
  }

  &__content {
    position: relative;
    z-index: 2;
  }

  &--secondary,
  &__outline {
    border: toRem(1) solid $primary-500;
    background: transparent;

    &:before {
      opacity: 0;
    }

    &:hover {
      background: $primary-500;
    }
  }

  &--dashed {
    border: toRem(2) dashed $secondary-500;
    font-size: toRem(20);
    font-weight: 600;
    transition: all 0.15s linear;
    padding: toRem(11) toRem(24);

    &:before {
      opacity: 0;
    }

    &:hover {
      border-color: transparent;
    }
  }

  &__active {
    background-color: $primary-500;
    box-shadow: 0 0 0 4px rgba(36, 93, 244, 0.20);
  }
}
