.contacts {
  display: flex;
  margin-top: toRem(96);
  gap: toRem(24);
  align-items: flex-start;

  @include max-tablet {
    margin-top: toRem(80);
    flex-direction: column-reverse;
  }
  @include max-mobile {
    margin-top: toRem(48);
  }

  &__form {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: toRem(32);
  }

  &__form-title {
    margin-bottom: toRem(12);
  }

  &__form-description {
    color: $text-color;
    display: flex;
    flex-direction: column;
    gap: toRem(12);
  }

  &__aside {
    border: toRem(1) solid $primary-700;
    background: $primary-900;
    border-radius: toRem(10);
    width: toRem(400);
    font-weight: 600;

    @include max-tablet {
      width: 100%;
    }
  }

  &__aside-heading {
    padding: toRem(24);
    font-size: toRem(20);
  }

  &__aside-row {
    display: flex;
    flex-direction: column;
    padding: toRem(16) toRem(24);
    border-top: toRem(1) solid $primary-700;
  }

  &__aside-row-label {
    text-transform: uppercase;
    font-size: toRem(14);
    line-height: toRem(24);
  }

  &__aside-row-link {
    color: $secondary-500;
    text-decoration: none;
    transition: color 0.15s linear;

    &:hover {
      color: $primary-200;
    }
  }
}