.skins {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: toRem(24) toRem(40);
  border-radius: toRem(12);
  border: toRem(2) solid $primary-700;
  background-color: $primary-700;
  padding: toRem(40);

  @media (max-width: $mobile) {
    flex-direction: column;
    padding: toRem(24);
  }

  &:hover {
    .skins__media {
      &:before {
        opacity: 0.2;
      }
    }

    .skins__img {
      transform: rotate(5deg);
    }
  }

  h3 {
    margin-bottom: toRem(16);
  }

  &__content {
    width: 100%;
    max-width: toRem(640);
  }

  &__link {
    display: inline-block;
    text-decoration: none;
    margin-top: toRem(40);
    text-align: center;

    &:before {
      z-index: 1;
    }
  }

  &__media {
    position: relative;
    width: 100%;
    max-width: toRem(440);
    height: toRem(400);

    @media (max-width: $tablet) {
      max-width: toRem(292);
      height: toRem(292);
    }

    @media (max-width: $mobile) {
      order: -1;
      max-width: toRem(343);
      height: toRem(343);
    }

    &:before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      width: 100%;
      height: 100%;
      transform: translate(-50%, -50%) translate3d(0, 0, 0);
      border-radius: 50%;
      background-color: $primary-500;
      filter: blur(36px);
      opacity: 0;
      z-index: 1;
      transition: opacity 0.5s;
    }
  }

  &__img {
    object-fit: cover;
    transform-origin: 50% 50%;
    transition: transform 0.5s;
  }
}
