.referrals-code {
  .description-section {
    margin-top: toRem(48);
  }

  &__promo {
    list-style-type: disc;
    padding-left: toRem(15);
    color: $primary-500;

    &--top-12 {
      margin-top: toRem(12);
    }
  }

  &__slider-box {
    margin-top: toRem(24);
  }

  &__combo-box {
    margin-top: toRem(24);

    @media (max-width: $tablet) {
      margin-top: toRem(16);
    }

    @media (max-width: $mobile) {
      margin-top: toRem(12);
    }

    .combo-box {
      &:not(:last-child) {
        margin-bottom: toRem(24);
      }

      @media (max-width: $tablet) {
        &:not(:last-child) {
          margin-bottom: toRem(16);
        }
      }

      @media (max-width: $mobile) {
        &:not(:last-child) {
          margin-bottom: toRem(12);
        }
      }
    }
  }
}
