.pros-cons-box {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: toRem(16) toRem(24);
  margin-top: toRem(24);

  @media (max-width: $tablet) {
    grid-gap: toRem(16);
    margin-top: toRem(16);
  }

  @media (max-width: $mobile) {
    grid-template-columns: 1fr;
  }
}

.pros-cons {
  border-radius: toRem(12);
  background-color: $primary-700;
  box-shadow: $element-shadow;
  padding: toRem(24);

  &__title {
    display: block;
    font-size: toRem(18);
    font-weight: 600;
    line-height: toRem(26);
  }

  p {
    margin-top: toRem(8);
  }

  &__list {
    margin-top: toRem(20);

    li {

    }

    &--pros {
      .pros-cons__item {
        &:before {
          background: url("../img/icons/outline-plus.svg") no-repeat center;
        }
      }
    }
  }

  &__item {
    position: relative;
    padding-left: toRem(32);

    &:not(:last-child) {
      margin-bottom: toRem(20);
    }

    &:before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background: url("../img/icons/outline-minus.svg") no-repeat center;
      width: toRem(24);
      height: toRem(24);
    }
  }
}
