.game-resource {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  border-radius: toRem(12);
  background-color: $resource-box-bg;
  padding: toRem(24);
  margin-top: toRem(-103);
  @include custom-scroll-bar;
  overflow-x: auto;
  z-index: 2;

  @media (max-width: $tablet) {
    gap: 0 toRem(24);
  }

  &__link {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: toRem(16);
    min-width: toRem(270);
    text-decoration: none;

    &:not(:last-child) {
      border-right: toRem(1) solid $grey-500;
    }

    @media (max-width: $tablet) {
      justify-content: flex-start;
      min-width: toRem(123);
      padding-right: toRem(24);
    }
  }

  &__icon-box {
    display: flex;
    align-items: center;
    justify-content: center;
    width: toRem(56);
    height: toRem(56);
    border-radius: toRem(4);
    background-color: $resource-icon-box;
    padding: toRem(8);
  }

  &__title {
    font-size: toRem(20);
    font-weight: 600;
    text-transform: uppercase;
    color: $white-color;
  }
}
