.media-links {
  display: flex;
  align-items: center;
  gap: toRem(12);
}

.media-link {
  display: block;
  width: toRem(32);
  height: toRem(32);
  border-radius: toRem(8);
  padding: toRem(8);
  transition: opacity 0.3s;

  &:hover {
    opacity: 0.8;
  }

  &__fb {
    background-color: $fb-color;
  }

  &__tw {
    background-color: $tw-color;
  }

  &__link {
    background-color: $text-color;
  }
}
