.faq-accordion {
  margin-top: toRem(24);

  @media (max-width: $tablet) {
    margin-top: toRem(16);
  }

  @media (max-width: $mobile) {
    margin-top: toRem(12);
  }

  &__item {
    border-radius: toRem(12);
    border: toRem(2) solid $primary-700;
    padding: toRem(24);
    transition: background-color 0.3s;
    overflow-y: hidden;

    &:not(:last-child) {
      margin-bottom: toRem(24);

      @media (max-width: $tablet) {
        margin-bottom: toRem(16);
      }

      @media (max-width: $mobile) {
        margin-bottom: toRem(12);
      }
    }

    &:hover {
      background-color: $primary-700;
    }

    &.active {
      background-color: $primary-700;
    }
  }

  &__header {
    font-size: toRem(18);
    font-weight: 500;
    line-height: toRem(28);
    text-decoration: none;
    color: $white-color;
    padding-right: toRem(28);
  }

  &__content {
    font-weight: 400;
    color: $text-color;

    p {
      &:not(:last-child) {
        margin-bottom: toRem(12);
      }
    }
  }
}
