.person-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: toRem(20) toRem(10);

  @media (max-width: $mobile) {
    flex-direction: column;
    align-items: flex-start;
  }

  &__content {
    display: flex;
    align-items: center;
    gap: toRem(16);
  }

  &__photo {
    flex-shrink: 0;
    width: toRem(48);
    height: toRem(48);
  }

  &__name {
    display: flex;
    align-items: center;
    gap: toRem(16);
    font-weight: 600;
    word-break: break-word;
    color: $white-color;
    margin-bottom: toRem(4);
  }

  &__status {
    position: relative;
    display: block;
    font-size: toRem(12);
    font-weight: 400;
    line-height: toRem(20);
    color: $secondary-500;
    padding-left: toRem(24);

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: toRem(18);
      height: toRem(18);
      background: url("../img/icons/expert.svg") no-repeat center;
    }
  }

  &__changes {
    font-size: toRem(14);
    font-weight: 400;
    line-height: toRem(24);
    color: $white-color;
  }
}
