.lang-switch {
  position: relative;
  max-width: toRem(87);
  padding-right: toRem(20);
  cursor: pointer;

  &:after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    width: toRem(16);
    height: toRem(16);
    background: url("../img/icons/arrow-down.svg") no-repeat center center;
    transition: transform 0.3s;
  }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    gap: toRem(4);
    font-size: toRem(16);
    font-weight: 400;
    line-height: normal;
    color: $text-color;
    text-decoration: none;
    z-index: 2;
  }

  &__drop-down {
    display: none;
    position: absolute;
    top: 206%;
    right: 0;
    width: toRem(137);
    border-radius: toRem(12);
    border: toRem(1) solid $primary-700;
    background-color: $primary-900;
    padding: toRem(16);
    z-index: 2;

    @media (max-width: $tablet) {
      top: 239%;
    }

    &:after {
      content: "";
      position: absolute;
      top: -3rem;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
    }

    .lang-switch__item {
      gap: toRem(6);

      &.active {
        color: $primary-500;
      }

      &:not(:last-child) {
        margin-bottom: toRem(20);
      }
    }
  }

  &:hover {
    &:after {
      transform: translateY(-50%) rotate(-180deg);
    }

    .lang-switch__drop-down {
      display: block;
    }
  }
}
