.common-info {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-top: toRem(32);
  padding-bottom: toRem(40);

  @media (max-width: $mobile) {
    flex-direction: column;
    gap: toRem(16) 0;
  }

  &__text {
    font-size: toRem(12);
    font-weight: 400;
    color: $text-color;
    margin-bottom: toRem(16);
  }

  &__link-domain {
    display: block;
    width: toRem(184);
    height: toRem(24);
  }

  &__link {
    display: flex;
    align-items: center;
    gap: 0 toRem(8);
    font-size: toRem(14);
    font-weight: 600;
    line-height: toRem(24);
    color: $text-color;
    text-decoration: none;
  }
}
