@keyframes errorAnimation {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes showImg {
  0% {
    opacity: 0;
  }

  5% {
    opacity: 1;
  }

  10% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

.error-page {
  color: $white-color;
  text-align: center;
  position: relative;
  min-height: toRem(640);

  &,
  &__content {
    flex: 1;
    display: flex;
    flex-direction: column;
  }

  &__content {
    justify-content: center;
  }

  &__title {
    margin-bottom: toRem(12);
  }

  &__text {
    margin-bottom: toRem(32);
  }

  &__nav {
    display: flex;
    justify-content: center;
    gap: toRem(16);
    flex-wrap: wrap;
  }

  &__nav-btn {
    padding: toRem(8) toRem(24);
  }

  &__animation {
    position: absolute;
    left: 50%;
    top: 50%;
    margin-top: 200px;
    transform: translate(-50%, -50%);
    width: 100%;
    padding-top: 100%;
    z-index: -1;

    @include max-tablet {
      margin-top: 150px;
    }

    @media (max-width: 800px) {
      width: toRem(728);
      height: toRem(728);
    }

    &:after,
    &:before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      opacity: 0.5;

    }

    &:after {
      background: url(../img/animate-img/error-animation-grid.svg) no-repeat 50% 50% / contain;

    }

    &:before {
      left: 6%;
      top: 6%;
      width: 88%;
      height: 88%;
      animation: errorAnimation 10s linear 0s infinite;
      background: conic-gradient(from 15.9deg at 50% 50%, rgba(3, 0, 20, 0) -103.84deg, rgba(0, 1, 20, 0) 187.2deg, rgba(99, 139, 241, 0.4) 256.12deg, rgba(3, 0, 20, 0) 256.16deg, rgba(0, 1, 20, 0) 547.2deg);
      border-radius: 10000px;

    }
  }

  &__animation-img {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: toRem(132);
    z-index: 2;
    pointer-events: none;
    opacity: 0;
    animation: showImg 10s linear 0s infinite;

    &._01 {
      margin-left: -25%;

      @include max-mobile {
        margin-left: -18%;
      }
    }

    &._02 {
      margin: -13% 0 0 -22%;
      animation-delay: 1s;

      @include max-tablet {
        margin: -19% 0 0 -29%;
      }
      @include max-mobile {
        margin: -17% 0 0 -20%;
      }
    }

    &._03 {
      margin: -20% 0 0 -9%;
      animation-delay: 1.83s;

      @include max-mobile {
        margin: -19% 0 0 -11%;
      }
      @include max-tablet {
        margin: -32% 0 0 -15%;
      }
    }

    &._04 {
      margin: -25% 0 0;
      animation-delay: 2.66s;

      @media (min-width: 1921px) {
        margin-top: -15%;
      }
      @include max-tablet {
        margin: -34% 0 0;
      }
      @include max-mobile {
        margin: -40% 0 0;
      }
    }

    &._05 {
      margin: -20% 0 0 11%;
      animation-delay: 3.3s;

      @include max-tablet {
        margin: -32% 0 0 13%;
      }
    }

    &._06 {
      margin: -13% 0 0 22%;
      animation-delay: 4.15s;

      @include max-tablet {
        margin: -19% 0 0 29%;
      }
      @include max-mobile {
        margin: -16% 0 0 19%;
      }
    }

    &._07 {
      margin-left: 25%;
      animation-delay: 5s;

      @include max-mobile {
        margin-left: 18%;
      }
    }
  }
}