.casino-games {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(toRem(165), 1fr));
  grid-gap: toRem(12);

  &__card-header {
    width: 100%;
    height: toRem(124);
    border-radius: toRem(8);
  }

  &__card-img {
    object-fit: cover;
    border-radius: toRem(8);
  }

  &__card-link {
    display: block;
    font-weight: 400;
    color: $text-color;
    text-decoration: none;
    margin-top: toRem(8);
    transition: all 0.3s;

    &:hover {
      color: $white-color;
    }
  }
}
