.gambling-payment-methods {
  h2 {
    font-size: toRem(28);
    font-weight: 700;
    line-height: toRem(36);
    color: $white-color;
    margin-top: toRem(24);
  }

  &__cards {
    .gambling-sites-cards {
      grid-template-columns: repeat(auto-fill,minmax(toRem(282), 1fr));
    }

    .gambling-sites-card__header {
      height: toRem(180);

      @media (max-width: $desktop) {
        height: toRem(240);
      }
    }

    .gambling-sites-card__content {
      padding: toRem(24);

      p {
        margin-top: 0;
      }
    }
  }

  &__content {
    margin-top: toRem(24);

    @media (max-width: $tablet) {
      margin-top: toRem(16);
    }
  }

  &__articles {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(toRem(540), 1fr));
    grid-gap: toRem(24);

    @media (max-width: $tablet) {
      grid-gap: toRem(16);
    }

    @media (max-width: $mobile) {
      grid-template-columns: 1fr;
    }
  }

  &__article {
    border-radius: toRem(12);
    background-color: $primary-900;
    padding: toRem(24);

    &--with-bg {
      background: url("../img/payment.png") center no-repeat;
      background-size: cover;
    }
  }

  h3 {
    font-size: toRem(22);
    font-weight: 600;
    line-height: toRem(30);
  }

  &__article-content {
    max-height: toRem(130);
    padding: 0 toRem(12) 0 0;
    overflow-y: auto;
    @include custom-scroll-bar;

    @media (max-width: $mobile) {
      max-height: toRem(230);
    }
  }

  &__payments {
    .gambling-sites-card__img-box {
      border-bottom: toRem(1) solid $primary-700;
    }

    .gambling-sites-card__img-box:before {
      background-color: unset;
    }

    .gambling-sites-card__content {
      p {
        margin-top: 0;
        text-transform: uppercase;
      };
    }
  }
}
