.information-list {
  position: relative;
  padding: toRem(24);
  border-radius: toRem(16);
  background-color: $primary-900;

  @media (max-width: $tablet) {
    background-color: $primary-700;
  }

  &:after {
    content: "";
    @include blur-box(57px);
    width: 30%;
    left: auto;
    right: 0;
    transform: translate(0, -50%) translate3d(0, 0, 0);
    opacity: 0;
  }

  &:hover {
    &:after {
      opacity: 0.1;
    }
  }

  &:not(:last-of-type) {
    margin-bottom: toRem(12);
  }

  p {
    font-weight: 400;
    color: $white-color;
    margin: 0;
  }

  strong {
    font-weight: 600;
  }
}
