.best-resource {
  &__wrap {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: toRem(16);

    @media (max-width: $tablet) {
      flex-direction: column;
      align-items: center;
      gap: toRem(16);
    }
  }

  &__content {
    position: relative;
    width: 100%;
    max-width: toRem(678);
    z-index: 1;

    @media (max-width: $tablet) {
      width: 100%;
      max-width: 100%;
    }
  }

  &__title {
    font-size: toRem(52);
    font-weight: 700;
    line-height: toRem(64);
    color: $white-color;

    @media (max-width: $tablet) {
      margin-top: 0;
    }

    @media (max-width: $mobile) {
      font-size: toRem(36);
      line-height: toRem(48);
    }
  }

  &__description {
    margin-top: toRem(24);
    font-weight: 400;
    color: $text-color;

    &--item {
      display: block;

      a {
        color: $primary-500;

        &:hover {
          text-decoration: none;
        }
      }

      &:not(:last-child) {
        margin-bottom: toRem(12);
      }
    }

    @media (max-width: $tablet) {
      margin-top: toRem(16);
    }

    @media (max-width: $mobile) {
      margin-top: toRem(16);
    }
  }

  &__poster {
    width: 100%;
    max-width: toRem(522);
    height: toRem(522);

    @media (max-width: $mobile) {
      max-width: toRem(343);
      height: toRem(343);
    }

    &--small {
      max-width: toRem(480);
      height: auto;
    }

    &--img {
      object-fit: contain;

      @media (max-width: $tablet) {
        object-fit: cover;
      }
    }
  }

  &__nav {
    display: flex;
    padding-top: toRem(24);
    gap: toRem(24);

    @include max-mobile {
      flex-direction: column;
    }

    .button--secondary {
      color: $secondary-500;

      span {
        color: $white-color;
      }
    }
  }
}
