.page-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: toRem(24);

  @media (max-width: $tablet) {
    flex-direction: column;
    align-items: flex-start;
  }

  @media (max-width: $mobile) {
    gap: toRem(16);
  }

  &__content {
    width: 100%;
    max-width: toRem(704);

    @media (max-width: $tablet) {
      max-width: unset;
    }
  }

  &__text {
    margin-top: toRem(16);

    p {
      color: $white-color;
      font-weight: 400;

      &:not(:last-child) {
        margin-bottom: toRem(12);
      }
    }
  }

  &__media {
    max-width: toRem(400);

    @media (max-width: $tablet) {
      order: -1;
      margin: 0 auto;
    }
  }
}
