.info-table {
  border: toRem(1) solid $primary-700;
  background: $primary-900;
  border-radius: toRem(12);
  margin: toRem(16) 0 toRem(28);

  @include max-mobile {
    font-size: toRem(16);
  }

  &__row {
    display: flex;
    padding: toRem(16) toRem(24);
    border-top: toRem(1) solid $primary-700;

    &:first-child {
      border-top: 0;
    }
  }

  &__row-heading {
    width: toRem(180);
    display: flex;
    align-items: center;
    padding-right: toRem(10);
    margin-top: 0;

    @include max-mobile {
      width: toRem(100);
    }
  }

  body &__row-icon {
    width: toRem(20);
    margin-right: toRem(8);
    margin-top: 0;
  }

  &__row-description {
    flex: 1;
  }
}