.codes {
  padding-top: toRem(40);

  @media (max-width: $tablet){
    padding-top: toRem(120);
  }

  &__select-type {
    display: flex;
    justify-content: center;
    gap: toRem(16);
    padding-top: toRem(40);

    .button {
      padding: toRem(8) toRem(24);
    }
  }

  .gambling-sites-card {
    perspective: 1000px;
    height: 400px;

    .card-inner {
      position: relative;
      width: 100%;
      height: 100%;
      transition: transform 0.6s;
      transform-style: preserve-3d;
    }

    .front-content,
    .back-content {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      backface-visibility: hidden;
    }

    .back-content {
      padding: 24px;
      color: $white-color;
      text-align: center;
      transform: rotateY(180deg);
    }

    &:hover {
      .card-inner {
        transform: rotateY(180deg);
      }
    }
  }
}
