.offers {
  background: $primary-900;
  border: toRem(1) solid $primary-700;
  border-radius: toRem(10);

  &--sticky {
    position: sticky;
    top: 0;

    @include max-tablet {
      position: static;
    }
  }

  &__heading {
    padding: toRem(24);
    font-weight: 600;
    font-size: toRem(20);
  }

  &__item {
    color: $white-color;
    display: flex;
    flex-wrap: wrap;
    text-decoration: none;
    padding: toRem(16) toRem(24);
    border-top: toRem(1) solid $primary-700;
    gap: toRem(12);

    &:hover {
      .offers {
        &__item-name {
          color: $primary-200;
        }
      }
    }
  }

  &__item-overline {
    width: 100%;
    font-weight: 600;
    text-transform: uppercase;
  }

  &__item-name {
    font-weight: 600;
    color: $secondary-500;
    transition: color 0.15s linear;
  }

  &__item-special {
    font-size: toRem(14);
    font-weight: 400;
  }
}
