.how-work-scheme {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: toRem(24);
  margin: toRem(24) 0;
  padding: 0 toRem(48);

  @media (max-width: $tablet) {
    margin: toRem(16) 0;
  }

  @media (max-width: $mobile) {
    margin: toRem(12) 0;
    padding: 0 toRem(36);
  }

  &__content {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: toRem(24);
    width: calc(50% - 36px);
    background-color: $primary-700;
    border-radius: toRem(16);
    padding: toRem(24);

    @media (max-width: $tablet) {
      width: 100%;
    }

    &:before,
    &:after {
      content: "";
      position: absolute;
      top: 50%;
      transform: translate(0, -50%);
      width: toRem(24);
      height: toRem(24);
      background: url("../img/icons/arrow-right.svg") no-repeat center;
    }

    &:before {
      display: none;
      left: toRem(-48);

      @media (max-width: $tablet) {
        display: block;
      }

      @media (max-width: $mobile) {
        left: toRem(-36);
      }
    }

    &:after {
      right: toRem(-48);

      @media (max-width: $mobile) {
        right: toRem(-36);
      }
    }

    &:last-child {
      &:after {
        display: none;
      }
    }

    &:nth-child(2n+3) {
      &:before {
        display: block;
      }
    }

    @media (max-width: $tablet) {
      &:first-child {
        &:before {
          display: none;
        }
      }
    }
  }

  &__shadow {
    content: "";
    position: absolute;
    top: 50%;
    right: 10%;
    width: toRem(90);
    height: 50%;
    transform: translate(0 -55%) translate3d(0, 0, 0);
    border-radius: 50%;
    background-color: $primary-500;
    filter: blur(30px);
    opacity: 0;
    z-index: 1;
    transition: opacity 0.3s;

    @media (max-width: $mobile) {
      display: none;
    }
  }

  &__description {
    color: $white-color;
  }

  &__image-box {
    position: relative;
    display: block;
    flex-shrink: 0;
    width: toRem(128);
    height: toRem(90);
    z-index: 2;

    @media (max-width: $mobile) {
      display: none;
    }
  }

  &__image {
    object-fit: cover;
  }
}

.how-work-scheme__content:hover {
  .how-work-scheme__shadow {
    opacity: 1;
  }
}
