.promo {
  margin-top: toRem(40);

  @media (max-width: $tablet){
    padding-top: toRem(120);
  }

  .best-resource {
    &__wrap {
      align-items: center;

      @include max-mobile {
        flex-direction: column-reverse;
      }
    }
    &__poster {
      height: auto;
      display: flex;
      justify-content: flex-end;
      max-width: 100%;

      @include max-mobile {
        justify-content: center;
      }

      img {
        object-fit: none;
        width: auto;

        @include max-mobile {
          width: toRem(265);
          height: auto;
          object-fit: contain;
        }
      }
    }
  }
}
