.page {
  display: flex;
  flex-direction: column;
  align-content: center;
  min-height: 100vh;

  &__main {
    flex: 1;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    position: relative;

    &.inner {
      z-index: 1;
    }
  }
}

div.page {
  &:not(.no-hidden) {
    overflow: hidden;
  }
}
