.table {
  width: 100%;
  margin-top: toRem(24);
  border-collapse: separate;
  border-spacing: 0 toRem(12);

  &__cell {
    padding: 0 toRem(16) toRem(12) toRem(16);
    vertical-align: middle;
    color: $text-color;
    font-weight: 400;
    word-break: break-word;

    &:first-child {
      padding-left: toRem(12);
    }

    &:last-child {
      padding-right: toRem(12);
    }
  }

  &__body {
    tr {
      &:nth-child(2n) {
        td {
          background-color: $primary-700;
        }
      }
    }
  }

  &__body-cell {
    border: toRem(2) solid $primary-700;
    padding: toRem(16);
    border-right: 0;
    border-left: 0;

    &:first-child {
      border-radius: toRem(12) 0 0 toRem(12);
      border-left: toRem(2) solid $primary-700;
    }

    &:last-child {
      border-radius: 0 toRem(12) toRem(12) 0;
      border-right: toRem(2) solid $primary-700;
    }
  }

  &__header-box {
    display: flex;
    align-items: center;
    gap: toRem(4);
    font-weight: 600;
    color: $white-color;

    &--1 {
      width: toRem(120);
    }

    &--2 {
      width: toRem(240);
    }

    &--3 {
      width: toRem(130);
    }

    &--4,
    &--5 {
      width: toRem(275);
    }
  }

  &__header-icon {
    width: toRem(20);
    height: toRem(20);
  }

  &__logo-box {
    width: toRem(60);
    margin-bottom: toRem(8);
  }

  &__logo {
    object-fit: cover;
  }

  &__title {
    font-weight: 500;
  }
}
