.text-page {
  // padding: toRem(24) 0;

  .breadcrumbs {
    width: 100%;
  }

  &__header {
    text-align: center;
    width: toRem(900);
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: toRem(24);
    margin: 0 auto;
  }

  &__header-text {
    display: flex;
    flex-direction: column;
    gap: toRem(20);

    p {
      margin: 0;
    }

    a {
      color: $white-color;
      transition: color 0.15s linear;

      &:hover {
        color: #245df4;
      }
    }
  }

  .description-section {
    margin-top: toRem(96);

    @include max-tablet {
      margin-top: toRem(48);
    }
  }

}
