.cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(toRem(234), 1fr));
  grid-gap: toRem(32) toRem(24);

  @media (max-width: $tablet) {
    grid-gap: toRem(32) toRem(16);
  }

  @media (max-width: $mobile) {
    grid-gap: toRem(12) toRem(16);
  }
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  font-size: toRem(14);
  font-weight: 400;
  line-height: toRem(24);
  color: $white-color;
  padding: toRem(24);
  border-radius: toRem(12);
  border: 1px solid $primary-700;
  background-color: $primary-900;
  box-shadow: $element-shadow;
  overflow: hidden;

  &:after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 40%;
    transform: translate(-50%, -50%) translate3d(0, 0, 0);
    border-radius: 50%;
    background-color: $primary-500;
    filter: blur(200px);
    opacity: 0;
    z-index: 1;
    transition: opacity 0.3s;
  }

  &:hover {
    &:after {
      opacity: 0.6;
    }
  }

  &__label {
    position: absolute;
    top: toRem(-10);
    left: toRem(-51);
    width: toRem(105);
    font-size: toRem(9);
    font-weight: 700;
    line-height: toRem(11);
    letter-spacing: -0.225px;
    text-align: center;
    color: $primary-900;
    background-color: $secondary-500;
    padding: toRem(6) toRem(29) toRem(8);
    transform: rotate(-45deg);
  }

  &__number {
    position: absolute;
    top: toRem(-12);
    left: toRem(-12);
    width: max-content;
    font-size: toRem(14);
    font-weight: 600;
    color: $white-color;
    padding: toRem(2) toRem(7);
    border-radius: toRem(4);
    background-color: $grey-500;
  }

  &__content {
    position: relative;
    z-index: 2;
  }

  &__logo {
    width: toRem(166);
    height: toRem(58);
    margin: 0 auto;

    &--img {
      object-fit: contain;
    }
  }

  &__title {
    font-size: toRem(20);
    font-weight: 600;
    line-height: toRem(26);
    text-align: center;
    margin-top: toRem(20);
  }

  &__rating {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: toRem(12);
    margin-top: toRem(16);
  }

  &__rating-box {
    display: flex;
    align-items: center;
    gap: toRem(2);

    &--item {
      display: block;
      width: toRem(20);
      height: toRem(20);
      background: url("../img/betting-img/star.svg") no-repeat center;
      fill: $primary-900;
      filter: drop-shadow(0 0 toRem(210) rgba(67, 103, 199, 0.35));
    }
  }

  &__benefits {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: toRem(61);
    margin-top: toRem(20);
  }

  &__benefits-row {
    display: flex;
    align-items: center;
    gap: toRem(8);

    &--bonus {
      position: relative;
      font-size: toRem(20);
      font-weight: 600;
      padding-left: toRem(28);

      &:before {
        content: "";
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        width: toRem(20);
        height: toRem(20);
        background: url("../img/betting-img/icon-1.svg") no-repeat center;
        background-size: contain;
      }
    }

    &:not(:last-child) {
      margin-bottom: toRem(8);
    }
  }

  &__info {
    margin-top: toRem(24);
  }

  &__info-item {
    position: relative;
    padding-left: toRem(24);

    &:before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: toRem(16);
      height: toRem(16);
      background: url("../img/betting-img/check.svg") no-repeat center;
      background-size: contain;
    }

    &:not(:last-child) {
      margin-bottom: toRem(12);
    }
  }

  &__actions {
    margin-top: toRem(24);
  }

  &__button {
    width: 100%;

    &:before {
      z-index: 1;
    }

    &:not(:last-child) {
      margin-bottom: toRem(12);
    }
  }
}

