.promotions {
  &__cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(toRem(236), 1fr));
    grid-gap: toRem(16) toRem(24);
    margin-top: toRem(24);

    @media (max-width: $tablet) {
      grid-gap: toRem(16);
      margin-top: toRem(16);
    }

    @media (max-width: $mobile) {
      grid-gap: toRem(12);
      margin-top: toRem(12);
    }
  }

  &__card {
    border-radius: toRem(12);
    border: toRem(1) solid $primary-700;
    background-color: $primary-900;
    padding: toRem(24);

    &:hover {
      .promotions__media {
        &:before {
          opacity: 0.1;
        }
      }
    }
  }

  &__media {
    position: relative;
    width: 100%;
    max-width: toRem(325);
    height: toRem(240);
    margin: 0 auto toRem(24);

    &:before {
      content: "";
      @include blur-box(36px);
    }

    .img {
      object-fit: contain;
    }
  }

  &__content {
    h4 {
      font-size: toRem(22);
      font-weight: 600;
      line-height: toRem(30);
      color: $white-color;
      margin-bottom: toRem(12);

      @media (max-width: $mobile) {
        margin-bottom: toRem(8);
      }
    }

    p {
      font-weight: 400;
      color: $text-color;
    }
  }

  &__link {
    color: $text-color;
    transition: color 0.3s;

    &:hover {
      color: $primary-500;
    }
  }
}
