.tooltip {
  font-size: toRem(12);
  font-weight: 400;
  line-height: toRem(18);
  color: $white-color;
  text-align: center;
  border-radius: toRem(4);
  border: toRem(1) solid $primary-900;
  background-color: $primary-900;
  box-shadow: $tooltip-shadow;
  padding: toRem(12);
  pointer-events: none;

  &::after {
    content: '';
    position: absolute;
    top: 1.5%;
    left: 50%;
    width: 0;
    height: 0;
    border: toRem(10) solid transparent;
    border-bottom-color: $primary-900;
    border-top: 0;
    margin-left: toRem(-13);
    margin-top: toRem(-13);
  }
}
