.slider {
  border-radius: toRem(12);
  border: toRem(1) solid $primary-700;
  background-color: $primary-900;
  margin-top: toRem(24);
  padding: toRem(24);

  &__content {
    max-height: toRem(246);
    padding-right: toRem(12);
    overflow-y: auto;
    @include custom-scroll-bar;
  }

  &__content-wrap {
    h4 {
      font-size: toRem(22);
      font-weight: 600;
      line-height: toRem(30);
      color: $white-color;
    }

    p {
      color: $text-color;
      margin-top: toRem(12);
    }
  }

  &__list {
    list-style-type: disc;
    color: $text-color;
    padding-left: toRem(24);
    margin-top: toRem(12);
  }
}

.slider-utils {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: toRem(24);

  .swiper-button-next,
  .swiper-button-prev,
  .swiper-pagination {
    position: relative;
    top: 0;
    left: 0;
    bottom: 0;
  }

  &__action {
    display: flex;
    align-items: center;
    gap: toRem(16);
  }

  .swiper-button-next,
  .swiper-button-prev {
    width: toRem(40);
    height: toRem(40);
    margin: 0;

    &:after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: toRem(24);
      height: toRem(24);
      background: url("../img/icons/arrow-right.svg") no-repeat center;
      z-index: 2;
    }
  }

  .swiper-button-next {
    &:after {
      transform: translate(-50%, -50%) rotate(-180deg);
    }
  }

  .swiper-pagination {
    width: auto;
    font-weight: 600;
    color: $text-color;
  }
}
