$sectionWidth: calc(100% - toRem(400));

.description-section {

  &:after {
    content: "";
    display: table;
    clear: both;
  }

  @include max-tablet {
    display: flex;
    flex-direction: column;
  }

  &__content {
    float: left;
    margin-right: toRem(24);
    max-width: calc(100% - toRem(424));

    @include max-tablet {
      max-width: 100%;
      margin-right: 0;
      float: none;
      order: 3;
    }

    h2,h3 {
      color: $white-color;
    }

    h2 {
      font-size: toRem(28);
      font-weight: 700;
      line-height: toRem(36);
      margin-top: toRem(48);

      &:first-child {
        margin-top: 0;
      }
    }

    h3 {
      font-size: toRem(22);
      font-weight: 600;
      line-height: toRem(30);
      margin-top: toRem(24);

      @media (max-width: $mobile) {
        margin-top: toRem(16);
      }
    }

    p {
      margin-top: toRem(12);
    }

    a {
      color: $primary-500;

      &.button {
        color: $white-color;
      }

      &:hover {
        text-decoration: none;
      }
    }

    h2,
    h3 {
      position: relative;
      color: $white-color;

      .section-anchor {
        position: absolute;
        top: toRem(-12);

        @include max-tablet {
          top: toRem(-150);
        }
      }
    }

    strong {
      font-weight: 600;
    }

    ul {
      &:not(.slider__list) {
        li {
          position: relative;
          padding-left: toRem(25);

          &:before {
            content: "";
            position: absolute;
            left: toRem(11);
            top: toRem(12);
            width: toRem(2);
            height: toRem(2);
            background: $white-color;
          }
        }
      }
    }

    p, h2, h3 {
      + ul {
        margin-top: toRem(12);
      }
    }

    .why-use ul li {
      padding-left: 0;
    }

    .button {
      display: inline-block;
      padding: toRem(12) toRem(24);
    }

    img {
      &:not(:first-child) {
        margin-top: toRem(16);
      }
    }
  }

  &__aside {
    position: sticky;
    top: 0;
    width: toRem(400);
    overflow: hidden;

    @include max-tablet {
      position: static;
      order: -1;
      width: 100%;
      margin-bottom: toRem(16);
      overflow: visible;
    }

    &.sticky {
      position: static;
      top: 0;
    }
  }

  &__nav {
    position: sticky;
    top: 0;
    width: toRem(400);
    //overflow: hidden;
    background: $primary-700;
    box-shadow: $element-shadow;
    z-index: 3;
    float: left;
    margin-bottom: toRem(24);

    @include max-tablet {
      order: -1;
      width: 100%;
      margin-bottom: toRem(24);
      overflow: visible;
      float: none;
    }

    @media (max-width: $tablet) {
      top: toRem(77);
    }

    @media (max-width: $mobile) {
      margin-bottom: toRem(12);
    }

    &.active {
      .description-section {
        &__nav-heading {
          &:before {
            display: none;
          }
        }
      }
    }
  }

  &__nav-heading {
    text-transform: uppercase;
    font-weight: 600;
    color: $white-color;
    padding: toRem(24);
    position: relative;
    cursor: pointer;

    &:before,
    &:after {
      content: "";
      position: absolute;
      background: $white-color;
      top: 50%;
      transform: translateY(-50%);
    }

    &:before {
      width: toRem(2);
      height: toRem(16);
      right: toRem(37);
    }

    &:after {
      height: toRem(2);
      width: toRem(16);
      right: toRem(30);
    }
  }

  &__nav-body {
    padding: toRem(24);
    display: flex;
    flex-direction: column;
    gap: toRem(20);
    background: $primary-700;
    counter-reset: point;
    position: absolute;
    left: 0;
    top: 100%;
    width: 100%;
  }

  &__nav:not(.active) {
    .description-section__nav-body {
      display: none;
    }
  }

  &__nav-row &__nav-link {
    &:before {
      content: counter(point)". ";
      counter-increment: point 1;
    }
  }

  &__nav-link {
    color: $white-color;
    text-decoration: none;
  }

  &__top-rated {
    margin-top: toRem(12);

    .top-rated {
      &:not(:last-child) {
        margin-bottom: toRem(24);

        @media (max-width: $tablet) {
          margin-bottom: toRem(16);
        }

        @media (max-width: $mobile) {
          margin-bottom: toRem(12);
        }
      }
    }
  }

  &__info {
    margin-top: toRem(24);

    @media (max-width: $tablet) {
      margin-top: toRem(16);
    }

    .combo-box {
      &:not(:last-child) {
        margin-bottom: toRem(24);

        @media (max-width: $tablet) {
          margin-bottom: toRem(16);
        }
      }
    }
  }

  @media (max-width: $mobile) {
    .slider {
      margin-top: toRem(16);
    }
  }

  &__casino-games {
    margin-top: toRem(12);
  }

  &__information-list {
    margin-top: toRem(24);
  }

  &__promo-codes {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: toRem(24);
    border-radius: toRem(16);
    background-color: $primary-700;
    gap: toRem(24);
    margin-top: toRem(24);

    @media (max-width: $tablet) {
      margin-top: toRem(16);
    }

    li {
      position: relative;
      width: 33.3%;
      color: $white-color;
      padding-left: toRem(20);
      word-break: break-word;

      &:before {
        content: "";
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        width: toRem(5);
        height: toRem(5);
        background-color: #fff;
        border-radius: 50%;
      }
    }
  }

  &__aside-actions {
    overflow: hidden;
    margin-top: toRem(24);
    background: $primary-700;
    padding: toRem(24);
    display: flex;
    flex-direction: column;
    gap: toRem(24);
    position: sticky;
    top: toRem(98);

    @include max-tablet {
      position: fixed;
      top: auto;
      left: 0;
      bottom: 0;
      width: 100%;
      flex-direction: row;
      align-items: center;
      background: $primary-900;
      z-index: 4;
    }

    .button {
      @include max-mobile {
        text-align: center;
        flex: 1;
      }
    }
  }

  &__aside-actions-title {
    text-align: center;

    @include max-tablet {
      flex: 1;
      text-align: left;
    }
    @include max-mobile {
      display: none;
    }
  }

  &__text-indent {
    margin-top: toRem(24);

    p {
      margin-top: 0;
    }

    @media (max-width: $tablet) {
      margin-top: toRem(16);
    }
  }

  .user-info {
    &__photo,
    &__actions-link {
      img {
        margin-top: 0;
      }
    }

    &__link {
      a {
        color: $white-color;
      }
    }

    &__text {

    }
  }
}
