.guides {
  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: $mobile) {
      flex-direction: column;
      align-items: flex-start;
      gap: toRem(16);
    }
  }

  &__header-link {
    padding: toRem(12) toRem(24);
    text-decoration: none;
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(toRem(384), 1fr));
    grid-gap: toRem(16) toRem(24);
    margin-top: toRem(24);

    @media (max-width: $tablet) {
      grid-template-columns: repeat(auto-fit, minmax(toRem(344), 1fr));
      grid-gap: toRem(16);
      margin-top: toRem(16);
    }

    @media (max-width: $mobile) {
      grid-template-columns: 1fr;
      grid-gap: toRem(12);
      margin-top: toRem(12);
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    position: relative;
    border-radius: toRem(12);
    border: toRem(1) solid $primary-700;
    background-color: $primary-900;
    text-decoration: none;
    z-index: 1;

    &:after {
      content: "";
      width: 50%;
      height: 50%;
      @include blur-box(77px);
      opacity: 0;
      z-index: -1;
    }

    &:hover {
      &:after {
        opacity: 0.1;
      }
    }
  }

  &__card-img-box {
    width: 100%;
    height: toRem(240);
  }

  &__card-img {
    object-fit: cover;
    border-radius: toRem(12) toRem(12) 0 0;
  }

  &__card-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-grow: 1;
    padding: toRem(16) toRem(24) toRem(24) toRem(24);
    color: $white-color;

    @media (max-width: $tablet) {
      padding: toRem(13) toRem(24) toRem(24) toRem(24);
    }

    @media (max-width: $mobile) {
      padding: toRem(16);
    }
  }

  &__card-content-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: toRem(16);
  }

  &__card-content-main {
    font-size: toRem(18);
    font-weight: 500;
    line-height: toRem(28);
    margin-top: toRem(13);

    strong {
      @include multiline-ellipsis(2);
    }
  }

  &__card-content-text {
    color: $text-color;
    margin-top: toRem(13);

    p {
      @include multiline-ellipsis(2);
    }
  }
}
