.form {
  &__body {
    display: flex;
    flex-wrap: wrap;
    gap: toRem(24);
  }

  &__item {
    min-width: 40%;
    flex: 1;

    @include max-mobile {
      min-width: 100%;
    }
  }
}

.form-control {
  display: flex;
  flex-direction: column;
  gap: toRem(8);

  &__label {
    font-weight: 500;
  }

  &__input {
    padding: toRem(10) toRem(12);
    font-size: toRem(16);
    line-height: toRem(26);
    font-family: inherit;
    border-radius: toRem(8);
    border: toRem(1) solid $primary-700;
    color: $primary-900;
    background: $text-color;

    &,
    &:focus,
    &:active {
      outline: none;
    }

    &::placeholder {
      color: $gray;
    }
  }
}

textarea {
  height: toRem(178);
  resize: vertical;
}
