.article-section {

  .description-section__content {
    @include max-tablet {
      order: unset;
    }
  }

  .guides {
    width: toRem(400);
    margin-top: toRem(24);
    overflow: hidden;

    @include max-tablet {
      width: 100%;
      order: unset;
      margin: 0 0 toRem(24) 0;
      overflow: visible;
    }

    .guides__card {
      flex-direction: row;
      align-items: center;

      @include max-tablet {
        flex-direction: column;
        align-items: flex-start;
      }
    }

    .guides__card-img-box {
      flex-shrink: 0;
      width: toRem(161);
      height: 100%;

      @include max-tablet {
        flex-shrink: unset;
        width: 100%;
        height: toRem(240);
      }
    }

    .guides__card-img {
      border-radius: toRem(12) 0 0 toRem(12);

      @include max-tablet {
        border-radius: toRem(12) toRem(12) 0 0;
      }
    }

    .guides__card-content {
      width: 100%;
      padding: toRem(16);

      @include max-tablet {
        padding: toRem(16) toRem(24) toRem(24) toRem(24);
      }

      @media (max-width: $mobile) {
        padding: toRem(16);
      }
    }

    .guides__card-wrap {
      display: flex;
      flex-direction: column;
      gap: toRem(8);

      @include max-tablet {
        display: block;
      }
    }

    .guides__card-content-main {
      order: -1;
      margin-top: 0;

      @include max-tablet {
        order: unset;
        margin-top: toRem(13);
      }
    }

    .guides__card-content-row {
      flex-direction: column;
      align-items: flex-start;
      gap: toRem(8);

      @include max-tablet {
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        gap: toRem(16);
      }
    }
  }
}

.all-articles-nav-links {
  .gambling-sites-links-list {
    justify-content: flex-start;
  }
}
