.gambling-sites-cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(toRem(343), 1fr));
  grid-gap: toRem(24);
  margin-top: toRem(24);

  @media (max-width: $tablet) {
    grid-gap: toRem(16);
    margin-top: toRem(16);
  }

  @media (max-width: $mobile) {
    grid-gap: toRem(12);
    margin-top: toRem(12);
  }

  &--codes {
    .gambling-sites-card {
      &__img {
        object-fit: contain;
      }
    }
  }
}

.gambling-sites-card {
  display: block;
  position: relative;
  border-radius: toRem(12);
  border: toRem(1) solid $primary-700;
  background-color: $primary-900;
  text-decoration: none;

  &:after {
    content: "";
    @include blur-box(20px);
    width: 50%;
    height: 50%;
    transform: translate(-50%, -9%) translate3d(0, 0, 0);
    opacity: 0;
    z-index: 1;
  }

  &:hover {
    &:after {
      opacity: 0.1;
    }
  }

  &__header {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: toRem(240);
  }

  &__header-title {
    padding: toRem(16);
    z-index: 3;

    h4 {
      font-size: toRem(24);
      font-weight: 700;
      line-height: normal;
      text-transform: uppercase;
      text-align: center;
      color: $white-color;
    }
  }

  &__img-box {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: toRem(12) toRem(12) 0 0;

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.50);
      border-radius: toRem(12) toRem(12) 0 0;
      z-index: 2;
    }
  }

  &__img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    border-radius: toRem(12) toRem(12) 0 0;
    object-fit: cover;
    z-index: 1;

    &--contain {
      object-fit: contain;
    }
  }

  &__content {
    padding: toRem(16) toRem(24) toRem(24) toRem(24);
    font-size: toRem(18);
    font-weight: 500;
    line-height: toRem(28);
    color: $white-color;
  }
}
